let commentList = [
    { id: 1, user_id: 1, article_id: 1, re_username: 0, comment_content: '', created_time: '1569737245', reply_num: 0, like_number: 0, tread_number: 0, re_comment_id: 0 },
]

//
export function getArticleLists(classifyId) {
    let list = [];
    if (classifyId === '') {
        list = articleList;
    } else {
        articleList.forEach(item => {
            if (item.classifyId === classifyId) {
                list.push(item);
            }
        })
    }
    return list;
}
//
export function getArticle(id) {
    id = Number(id);
    if (id != NaN) {
        let article = {};
        articleList.forEach(item => {
            if (item.id === id) {
                article = item;
            }
        })
        return article;
    }
}
//
export function getComment(id) {
    id = Number(id);
    let comment = [];
    commentList.forEach(item => {
        if (item.article_id === id) {
            comment.push(item);
        }
    })
    datas.forEach(item => {
        if (item.re_comment_id == 0) {
          let comment = {};
          comment.head_img = item.user_header_img;
          comment.username = item.username;
          comment.createdTime = item.created_time;
          comment.content = item.comment_content;
          comment.reNumber = item.reply_number;
          comment.likeNumber = item.like_number;
          comment.tread_number = item.tread_number;
          comment.article_id = item.article_id;
          comment.comment_id = item.id;
          comment.user_id = item.uid;
          comment.reComment = [];
          data.unshift(comment);
        } else {
          data.forEach(x => {
            if (x.comment_id === item.re_comment_id) {
              let comment = {};
              comment.head_img = item.user_header_img;
              comment.username = item.username;
              comment.createdTime = item.created_time;
              comment.content = item.comment_content;
              comment.reNumber = item.reply_number;
              comment.likeNumber = item.like_number;
              comment.tread_number = item.tread_number;
              comment.article_id = item.article_id;
              comment.comment_id = item.id;
              comment.user_id = item.uid;
              if (item.re_username == "0") {
                comment.re_username = "";
              } else {
                comment.re_username = item.re_username;
              }
              x.reComment.push(comment);
            }
          });
        }
      });
}